.testimonial-container{
    background-image: url('../Asset/Dot-grid-why-us.png');
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 3%;
    padding-bottom: 3%;
    
}
.testimonial-subcontainer{
    display: flex;
    justify-content: center;
    width: 60%;
}
.testimonial-subcontainer h2{
    color:#FFC1AE;
}
.testimonial-subcontainer h1{
    font-size: 45px;
    font-family: 'Gruppo', sans-serif;
    line-height: 3.5rem;
}
.testimonial-subcontainer p{
    font-size: 25px;
}
/* .testimonial-subcontainer div:nth-child(){
    width:60%;
} */
.testimonial-subcontainer div:nth-child(2){
    background-color: #FFC1AE;
    border-radius: 15px;
}
.testimonial-subcontainer-left{
    width:70%;
    text-justify:inner-word;
}
.testimonial-subcontainer-right{
   display: flex;
   align-items: center;
   width: 60%;
    
}
.testimonial-subcontainer-right p{
    
    text-justify:inner-word;
    vertical-align: middle;
    font-size: 2rem;

}
.testimonial-subcontainer-right::before{

    content:url('../Asset/double-quotes.png');;
    background-repeat: no-repeat;
    border-radius: 50px;
    background-color: #FFC1AE;
    position: absolute;
    z-index: 10;
    margin-top: -15rem;
    margin-left:-20px;


}
