.tech-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top:3% ;
    padding-bottom: 3%;
    background-image: url('../Asset/Group.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.tech-tile-row{
    display: flex;
    margin-top:6%
}
.tech-tile{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px;
    width:200px;
    height:200px;
    background-color: #FFC1AE;
    border-radius: 10px;
    font-size: 25px;
}
