.services-container{
    display: flex;
    justify-content: center;
    margin-top: 3%;
}
.services-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 70px;
    font-family: 'Gruppo', sans-serif;
    background-image: url('../Asset/Dot-grid-why-us.png');
    /* background-repeat: no-repeat; */
    /* background-size:30rem; */
    width:370px;
    margin-right:-150px;
}
.services-tile-row{
    display: flex;
}
.services-tile{
    display: flex;
    flex-direction: column;
    /* justify-content: flex-end; */
    align-items: center;
    width:18rem;
    background-color: #FFC1AE;
    padding: 10px;
    padding-top:20px;
    padding-bottom: 20px;
    margin:10px;
    border-radius: 10px;
    font-size: 25px;
}
