.header{
    display: flex;
    justify-content: space-between;
    padding: 10px;;
}
.header-list{
    display: flex;
    list-style: none;
    font-size: 1.8rem;
}
.header-list li{
    margin-right: 1.5rem;
}
.header-list-and-button{
    display: flex;
    align-items: center;
    margin-right: 5%;
}
.header-start-project-button{
    display: flex;
    align-items:center;
    justify-content: center;
    width: 230px;
    height: 50px;
    flex-shrink: 0;
    border-radius: 10px 10px 12px 0px;
    background: #000;
    color:#fff;
    font-size: 1.5rem;
}
